@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,400&display=swap);
.title{width:100%;height:100vh;position:relative;display:flex;align-items:center}.title h2{font-size:4rem;font-weight:600;line-height:1;width:80%;margin:0;color:#fff;z-index:1000}@media (max-width: 767.98px){.title{height:25rem}.title h2{font-size:3rem;width:100%;text-align:left}}@media (max-width: 409.98px){.title h2{font-size:2.6rem}}

.title__bg{position:absolute;top:0;left:0;width:100%;height:100%;background-position:center;background-size:cover;background-repeat:no-repeat;box-shadow:inset 0px 0px 0px 1500px rgba(0,0,0,0.5)}

.content{margin:3rem 0}.content h3{font-weight:500;font-size:2.5rem;margin:2rem 0 0.5rem 0}.content h3:first-child{margin-bottom:0.5rem}.content p,.content i{text-indent:1rem;font-size:1.75rem;text-align:justify;margin:0;margin-bottom:1rem}.content i{display:block;font-style:italic;color:#333;margin:0 2rem 3rem}.content ul{margin:0;margin-bottom:1rem}.content li{font-size:1.6rem;margin-bottom:1rem}.content li:last-child{margin-bottom:0}

.slider{width:100%;height:100%}.slider-item{position:absolute;left:0;top:0;background-position:center;background-size:cover;background-repeat:no-repeat;opacity:0;-webkit-animation-timing-function:ease;animation-timing-function:ease;width:100%;height:100%;box-shadow:inset 0px 0px 0px 1500px rgba(0,0,0,0.5)}.slider__dots{position:absolute;bottom:2rem;left:50%;transform:translateX(-50%);z-index:150}.slider__dots-item{background:none;padding:0;border:0;width:1rem;height:1rem;margin-right:1rem;position:relative}.slider__dots-item:before{display:block;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);content:'';border-radius:50%;width:0.8rem;height:0.8rem;background-color:#ccc;transition:0.25s}.slider__dots-item.active:before{background-color:#fff;width:1rem;height:1rem}.slider__dots-item:last-child{margin-right:0}@-webkit-keyframes sliderItemPrevOpacity{0%{opacity:1}100%{opacity:0}}@keyframes sliderItemPrevOpacity{0%{opacity:1}100%{opacity:0}}

.separation{display:flex;justify-content:space-between;margin-bottom:2rem}@media (max-width: 767.98px){.separation{flex-wrap:wrap}.separation div:first-child{margin-bottom:2rem}}

.separation__content{width:60%}.separation__content h3:first-child{margin-top:0}.separation__content p:last-child{margin-bottom:0}.separation__content ul:last-child{margin-bottom:0}@media (max-width: 767.98px){.separation__content{width:100%}}

.separation__img{width:35%;border-radius:0.5rem;background-size:cover;background-position:center;background-repeat:no-repeat}@media (max-width: 767.98px){.separation__img{width:100%}}

.img{display:flex;justify-content:space-between;margin-top:2rem;margin-bottom:2rem}.img__item{width:47%;border-radius:0.5rem;background-position:center;background-size:cover;background-repeat:no-repeat}@media (max-width: 767.98px){.img{flex-wrap:wrap}.img__item{width:100%}.img__item:first-child{margin-bottom:2rem}}

.body-shadow{position:fixed;width:100%;height:100vh;background-color:#000;pointer-events:none;z-index:1800;transition:0.7s}.header{position:fixed;top:0;right:-300px;height:100vh;width:300px;background-color:#fff;transition:0.7s ease-in-out;overflow:auto;z-index:2000}.header.active{right:0}.header__open{position:absolute;top:16px;right:0;width:50px;margin-right:12px;height:28px;z-index:1500;cursor:pointer}.header__open div{width:45px;height:2.5px;position:absolute;left:50%;transform:translateX(-50%);background-color:#fff}.header__open div:nth-child(1){top:0}.header__open div:nth-child(2){top:50%;transform:translate(-50%, -50%)}.header__open div:nth-child(3){bottom:0}.header__close{width:32px;height:32px;margin:16px;margin-left:auto;position:relative;cursor:pointer}.header__close div{width:100%;height:2.5px;position:absolute;top:50%;left:50%;background-color:#999}.header__close div:first-child{transform:translate(-50%, -50%) rotateZ(45deg)}.header__close div:last-child{transform:translate(-50%, -50%) rotateZ(-45deg)}.header__item{text-decoration:none;display:flex;align-items:center;justify-content:flex-end;text-align:right;border-top:1px solid #999;padding:18px 16px;font-size:24px;color:#999;transition:0.5s}.header__item.active{color:#262626}.header__item:last-child{border-bottom:1px solid #999}

body{margin:0}*{font-family:'Roboto', sans-serif;font-size:1rem;font-weight:400;color:#262626;box-sizing:border-box;-webkit-tap-highlight-color:transparent}*:focus{outline:none}.wrapper{padding:0 15px}@media (min-width: 575.98px){.wrapper{padding:0 calc(50% - 270px)}}@media (min-width: 767.98px){.wrapper{padding:0 calc(50% - 360px)}}@media (min-width: 991.98px){.wrapper{padding:0 calc(50% - 480px)}}@media (min-width: 1199.98px){.wrapper{padding:0 calc(50% - 590px)}}@media (max-width: 991.98px){html{font-size:14px}}@media (max-width: 575.98px){html{font-size:12px}}

